header.banner {
  position: absolute;
  top: 0; left: 0; right: 0;

  body.analytics & {display: none;}
  
  div.wrap {

    a.content-jump {
      position: absolute;
      top: 0; left: -1000em;
      z-index: 1000;
      padding: $nav-link-padding 0.4rem;
      background: $clr-b1;
      text-align: center;
      color: white;
      text-decoration: none;
      outline: none;

      &:active,
      &:focus {left: 0; right: 0;}
    }
  }

  nav {
    position: relative;
    z-index: 100;
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    // logo

    p.logo {
      flex: 1 1 auto;
      position: relative;
      z-index: 3;
      margin: 0 1rem 0 0;
      font-size: $fs-22;

      body.landing.services & {
        > span {
          margin: 0 auto;
          padding-top: 2rem;
          //max-width: 250px;
          //background-image: url(../../assets/images/logos/mantra-systems-strapline-white.svg);
          
          @include bp-m {
            margin: 0;
            padding-top: 4rem;
            max-width: 280px;
          }
        }
      }

      a,
      > span {
        display: block;
        padding: 1.6rem 0 0.75rem;
        max-width: 200px;
        background: url(../../assets/images/logos/mantra-systems.svg) no-repeat 50% 72%; 
        background-size: 100% auto;
        text-decoration: none;

        body.nav-alt & {background-image: url(../../assets/images/logos/inverted-mantra-systems.svg);}

        @include bp-s {max-width: 250px;}

        @include bp-m {
          padding-top: $nav-link-padding;
          max-width: 250px;

          &.current {
            box-shadow: inset 0 5px $clr-b1;
            body.nav-alt & {box-shadow: inset 0 5px white;}
          }
        }

        &:before {
          display: block;
          content: "\00A0";
        }

        > span {@include hide-screen();}
      }

      a {
        @include bp-m {
          &:hover,
          &:focus {
            box-shadow: inset 0 5px $clr-b1;
            body.nav-alt & {box-shadow: inset 0 5px $clr-b1;}
          }
        }
      }
    }

    // menu toggle

    p.menu-toggle {
      position: fixed;
      top: 0; right: 0;
      z-index: 3;
      padding: 1rem 1rem 0 0;
      line-height: 1;
      cursor: pointer;

      @include bp-l {@include hide-screen();}

      label.button {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 3rem;
        padding: 0;
        width: 2.5rem;
        height: 2.5rem;
        background: $clr-b1;
        font-size: $fs-25;
        color: white;

        @include bp-m {
          width: 3rem;
          height: 3rem;
          font-size: $fs-30;
        }

        &:before {
          @extend .icon-extension,.icon-bars;
        }

        span {@include hide-screen();}
      }
    }

    // contact buttons

    ul.contact-buttons {display: none;}

    // links

    div.links {
      position: absolute;
      left: -1000em;
      text-align: left;

      @include bp-l {
        position: static;
        display: flex;
        align-items: center;
        flex: 0 1 auto;
        width: auto;
      }

      a {text-decoration: none;}

      ul.site {
        margin: 0;
        font-size: $fs-22;

        @include bp-m {
          display: flex;
          align-items: center;
          //position: relative;

          // &.main li a.text:before {
          //   // white arrow
          //   position: absolute;
          //   bottom: 0; left: 50%;
          //   z-index: 1;
          //   margin: 0 0 -20px -10px;
          //   border: 10px solid transparent;
          //   border-bottom-color: white;
          //   content: "";
          //   opacity: 0;
          // }

          &.associates li.return a.text:before {
            margin-right: 0.25rem;
            font-size: $fs-16;
          }
        }

        > li {

          @include bp-m {
            flex: 1 0 auto;

            &.current {
              box-shadow: inset 0 5px $clr-b1;

              body.nav-alt & {
                box-shadow: inset 0 5px white;
                span.title.new:after {color: white;}
              }

              //a {color: $clr-b0;}
            }

            &.contact,
            &.login {
              padding: 1.2rem 0 0 1.1rem;

              a {
                background: none;
                font-size: inherit;
                font-weight: 700;
                box-shadow: inset 0 0 0 2px $clr-n3;

                body.nav-alt & {
                  color: white;
                  box-shadow: inset 0 0 0 2px white;
                }
              }
            }

            // if mouse hover sub menu is to appear
            &.services,
            &.resources,
            &.company {
              @media (hover: hover) and (pointer: fine) { // excludes touch screens
                animation: preventPointerEvents 1s ease forwards;
                pointer-events: none;

                &:hover {
                  > a.text {

                    background: white;
                    color: $clr-b1;
                    box-shadow: inset 0 5px $clr-b1;
                    transition: background 0.2s 0s;

                    // &:before {
                    //   margin-bottom: 0;
                    //   opacity: 1;
                    //   transition: opacity 0.2s 0.25s, margin-bottom 0.1s 0.25s;
                    // }
                  }

                  div.sub {
                    top: 100%;
                    opacity: 1;
                    animation: popAppear 0.25s ease-out forwards;
                  }
                }
              }
            }

            //&.company div.sub.columns a:before {background: $clr-b1;}
          }

          > a.text {
            position: relative;
            display: block;
            padding: 0.5rem 0.75rem;
            color: white;

            span.snippet {@include hide-screen();}

            @include bp-m {
              flex: 1 0 auto;
              border-radius: 0;
              padding: $nav-link-padding 1rem 0.75rem;
              background: none;
              color: $clr-n3;
              font-weight: 700;

              &:hover,
              &:focus {
                color: $clr-b1;
                box-shadow: inset 0 5px $clr-b1;
              }

              body.nav-alt & {
                color: white;         
                &:hover,
                &:focus {color: lighten($clr-b1,15%);}
              }
            }
          }
        }
      }
          
      div.sub {
        @include bp-l {
          position: absolute;
          top: -100em; right: 0; left: 0;
          overflow: hidden;
          border-radius: 0.4rem;
          background: white;
          box-shadow: 0 3px 3px rgba(black,0.2);
          opacity: 0;

          ul {
            display: flex;
            flex-wrap: wrap;
            margin: 0;

            a {
              display: flex;
              align-items: center;
              transition: background-color 0.1s;
            }
          }

          &.link-blocks { 
            padding: 0.4rem;

            ul {
              flex-direction: row;

              li {
                display: flex;
                flex: 1 1 auto;
                padding: 0.5rem;
                width: 33.333%;

                &.sub-mdr {order: 2;}
                &.sub-ivdr {order: 3;}
                &.sub-heor {order: 1;}

                a {
                  border-radius: $border-radius;
                  padding: $spacing;
                  width: 100%;
                  background-color: rgba($clr-n4,0.5);
                  background-image:
                    linear-gradient(to top, rgba(white,0.5) 0%, transparent 50%),
                    url(../../assets/images/content/patterns/landscape-dark.svg);
                  background-size: 50px auto;
                  background-position: 50%;
                  text-align: center;

                  > span {
                    flex: 1 1 auto;
                    line-height: 1.2;

                    b {
                      position: relative;
                      display: block;
                      margin-bottom: $vmargin;
                      font-weight: 800;
                      font-size: $fs-20;
                      text-transform: uppercase;

                      &:after {
                        position: absolute;
                        left: 50%;
                        bottom: -1rem;
                        margin-left: -15px;
                        border-radius: 3px;
                        width: 30px;
                        height: 5px;
                        background: $clr-b1;
                        content: "";
                      }
                    }

                    span.snippet {
                      display: block;
                      //margin-bottom: 0.75rem;
                      color: $clr-1;
                      font-size: $fs-18;
                      font-weight: 400;
                    }
                  }

                  &:hover,
                  &:focus {background-color: rgba($clr-n4,0.75);}

                  &.current {
                    background-color: rgba($clr-n4,1);
                    b {color: $clr-b1;}
                  }
                }
              }
            }
          }

          &.link-list {
            border: 4px solid white;

            ul {
              margin: 0;
              font-size: $fs-18;

              li {
                flex: 1 1 auto;
                width: 33.33%;

                &.sub-about {order: 3;}
                &.sub-contact {order: 6;}
                &.sub-faqs {order: 1;}
                &.sub-jobs {order: 4;}
                &.sub-newsletter {order: 2;}
                &.sub-linkedin {order: 5;}

                &.sub-mdr-guide {order: 3;}
                &.sub-enablechat {order: 2;}
                &.sub-guides {order: 4;}
                &.sub-courses {order: 5;}
                &.sub-templates {order: 6;}
                &.sub-articles {order: 1;}

                a {
                  padding: calc($spacing/1.5);

                  &:before {
                    flex: 0 0 auto;
                    display: block;
                    margin-right: 0.5rem;
                    border-radius: 5px;
                    width: 2rem;
                    height: 2rem;
                    line-height: 2rem;
                    background: $clr-n3;
                    text-align: center;
                    font-size: $fs-25;
                    color: white;
                  }

                  > span {
                    flex: 1 1 auto;
                    line-height: 1.2;

                    b {
                      display: block;
                      font-weight: 800;
                      font-size: $fs-15;
                      text-transform: uppercase;
                    }

                    span.snippet {
                      display: block;
                      font-size: $fs-14;
                      font-weight: 400;
                      color: $clr-1;
                    }
                  }

                  &:hover,
                  &:focus {background: lighten($clr-n4,20%);}

                  &.current {
                    background: lighten($clr-n4,15%);
                    b {color: $clr-b1;}
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  // menu toggle 

  input#menu-toggle {
    @include hide-input();

    // -------------------------------------- menu open

    &:checked + nav {
      @extend .bg-1;
      display: block;
      position: fixed;
      left: 0; right: 0; bottom: 0; top: 0;
      z-index: 2000;
      padding-bottom: 3rem;
      overflow-y: auto;
      -webkit-overflow-scrolling: touch;

      // logo

      p.logo {
        position: absolute;
        top: 1rem; left: 1rem;
        z-index: 3;
        margin: 0;
        padding: 0;

        a {
          @extend .button;
          display: flex;
          align-items: center;
          justify-content: center;
          position: relative;
          z-index: 2;
          border-radius: 3rem;
          padding: 0;
          width: 2.5rem;
          height: 2.5rem;
          background: white;
          font-size: $fs-25;
          color: $clr-1;

          @include bp-m {
            width: 3rem;
            height: 3rem;
            font-size: $fs-30;

            &:hover,
            &:focus {
              background: $clr-b1;
              color: white;
            }
          }

          &:before {
            @extend .icon-extension, .icon-home;
            padding: 0;
          }

          > span {@include hide-screen();}
        }
      }

      // menu toggle

      p.menu-toggle label.button {
        &:before {@extend .icon-times;}
      }

      // contact buttons

      ul.contact-buttons {
        position: relative;
        z-index: 2;
        display: flex;
        justify-content: center;
        margin: 0;
        padding: 1rem 0;
        font-size: $fs-25;

        li {
          display: inline-block;
          margin: 0 0.18rem;

          @include bp-m {margin: 0 0.5rem;}

          a.button {
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            z-index: 2;
            border-radius: 3rem;
            padding: 0;
            width: 2.5rem;
            height: 2.5rem;
            background: white;
            font-size: $fs-25;
            color: $clr-1;

            @include bp-m {
              width: 3rem;
              height: 3rem;
              font-size: $fs-30;

              &:hover,
              &:focus {
                background: $clr-b1;
                color: white;
              }
            }

            > span {@include hide-screen();}
          }
        }
      }

      // links

      div.links {
        overflow: hidden;
        position: relative;
        left: auto;
        z-index: 2;
        margin: 0 1rem 2rem;
        border-radius: $border-radius;
        font-size: $fs-17;

        @include bp-m {
          align-items: baseline;
          padding: 0;
        }

        @include bp-m {font-size: $fs-18;}
        @include bp-l {font-size: $fs-20;}

        ul.site {
          @include bp-m {
            align-items: stretch;
            flex: 1;
          }

          &.associates {
            > li {

              &.return {
                @include bp-m {display: none;}

                a:before {
                  @include bp-m-less {display: none;}
                }
              }

              > a {border-bottom: 0;}
            }
          }
          
          > li {
            margin-bottom: $vmargin;
            background: rgba(black,0.3);

            @include bp-m-less {
              overflow: hidden;
              border-radius: $border-radius;
            }

            &:hover > a {box-shadow: none;}

            &.current {
              box-shadow: none;
              > a {background: rgba(black,0.4);}
            }

            @include bp-m {
              flex: 1 1 auto;
              margin: 0 4px 0 0;
              width: 25%;
              background: rgba(black,0.2);
            }

            &.services > a {border-bottom-color: $clr-b2;}
            &.resources > a {border-bottom-color: $clr-b3;}
            &.contact,
            &.login {display: none;} 

            &.company {
              margin: 0;
              > a {border-bottom-color: $clr-b4;}
            }

            > a {
              display: flex;
              flex-direction: column;
              border-bottom: 5px solid;
              padding: 0.6rem 1rem;
              background: rgba(black,0.1);
              font-size: $fs-25;
              font-weight: 300;
              color: white;
              transition: background-color 0.1s;

              @include bp-m {
                &:hover,
                &:focus {
                  background: rgba(white,0.05);
                  box-shadow: none;
                }
              }

              span.title.new:after {
                position: static;
                vertical-align: top;
                margin: 0 0 0 0.4rem;
              }

              span.snippet {
                @include reset-hide-screen();
                order: 2;
                font-size: $fs-16;
                font-weight: 400;
                color: #ccc;
              }
            }
          }
        }

        div.sub {
          position: static;
          margin: 0;
          border-radius: 0;
          width: auto;
          background: none;
          box-shadow: none;
          opacity: 1;
          animation: none;

          &:before {content: none;}

          ul {

            li {

              &.contact,
              &.login {display: block;}
              &.about {display: none;}

              a {
                border-radius: 0;
                color: white;
                box-shadow: 0 1px rgba(white,0.075);

                @include bp-m {
                  &:hover,
                  &:focus {background: rgba(white,0.05);}
                }

                &.current {
                  background: rgba(black,0.4);
                  box-shadow: inset 5px 0 $clr-b1;
                }

                span.snippet {
                  margin-top: 0.25rem;
                  color: #ccc;

                  b {
                    color: lighten($clr-b6,10%);
                    font-weight: 400;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
